import { Component } from 'react';
import styled from 'styled-components';

export const Wrapper = styled.div`
    //background: var(--darkGrey);    
    padding: 10px 10px;
    overflow: hidden;
    max-height: 100%;
    z-index:100;
    a {
        text-decoration: none;
    }
    
`;

export const Content = styled.div`
    background: linear-gradient(
        to bottom, 
        rgba(255,255,255,0) 20%, 
        rgba(255,255,255,0.0) 80%, 
        rgba(255,255,255,0.5) 100%
    ), url(${({image}) => image}), var(--mint-cream); 
    background-size: 150%;
    background-position: center;
    height: 200px;
    max-width: 720px;
    transition: all 0.3s;
    animation: animateThumb 0.5s;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.4);

    :hover {
        background:linear-gradient(
        to bottom, 
        rgba(255,255,255,0) 20%, 
        rgba(255,255,255,0.6) 70%, 
        rgba(255,255,255,0.9) 100%
    ),  url(${({image}) => image}), var(--mint-cream); 
        background-position: center;
        background-size: 175%;
        opacity:  0.8;
    }

    @keyframes animateThumb{
        from{
            opacity:  0;
        }
        to{
            opacity: 1;
        }
    }
`;

export const Image = styled.img`
    max-width: 720px;
    transition: all 0.3s;
    border-radius: 5px;
    object-fit: cover;
    animation: animateThumb 0.5s;
    width:100%;
    margin:auto;

    :hover {
        width: 105%;
        opacity:  0.8;
    }

    @keyframes animateThumb{
        from{
            opacity:  0;
        }
        to{
            opacity: 1;
        }
    }
`;

export const Title = styled.div`
    position:relative;
    bottom:-130px;
    color: var(--black);

    ${Content}:hover & {
        display: none;
    }

    font-family: verdana;
    text-align: center;
    font-size: 35px;
    color: rgb(255, 255, 255);
    text-shadow: rgb(0, 0, 0) 0px 3px 12px;
`;

export const Type = styled.div`
    height: 12%;
    position:relative;
    top:-50px;
    left:-7px;
    

    p {
        font-family: verdana;
        padding:8px;
        display: inline-flex;
        background-color: var(--white);
        color: rgb(0, 0, 0);
        font-size: 15px;
        border-radius: 5px;
    }

    ${Content}:hover & {
        display: none;
    }
`;

export const Description = styled.div`
    position:relative;
    bottom:-50px;
    color: var(--black);
    padding: 10px 20px;
    display: none;
    transition: all 0.3s;
    animation: slide-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

    ${Content}:hover & {
        display: block;
        position: relative;
        bottom: 15%;
    }

    @keyframes slide-top {
    0% {
        transform: translateY(150px);
        opacity: 0;
    }
    100% {
        transform: translateY(100px);
        opacity: 1;
    }
}
`;

export const Gap = styled.div`
    height:10px;
`;