import React from 'react';
import { Wrapper, Content, Image, Title, Description, Type } from './Projects.styles';

const Projects = ({title, image, description, link, type}) => (

    <Wrapper>
        {console.log(link)}
        <a href={link} target="_blank">
            <Content href={link} target="_blank" image={image}>
                {/* <Image src={image} alt='project-thumb' /> */}
                <Title>{title}</Title>
                <Type>
                    <p>{type}</p>
                </Type>
                <Description>{description}</Description>
            </Content>
        </a>
    </Wrapper>
)

export default Projects;